.form-control {
  margin-bottom: 0.5rem;
  color: #657b90;
}

.form-control:focus {
  color: #657b90;
  border: 3px solid #4a49be;
  outline: 0;
  box-shadow: none;
}
.form-control-error {
  color: crimson;
}
.form-control-error:focus {
  color: crimson;
  border: 3px solid #4a49be;
  outline: 0;
  box-shadow: none;
}

.btn {
  padding: 0.6rem 1.2rem;
  align-self: stretch;
}
.btn-primary:hover {
  box-shadow: darkgray;
  transition: 0.3s;
}
.heading {
  color: #000000;
  font-size: 40px;
  font-weight: 600;
}
.label-text {
  font-size: small;
  color: #48525b;
  opacity: 0.5;
}
